import React, { useEffect, useRef, useState } from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import RatingLabel from '../rating-label';
import Icon from '../icon';

const CSS_PREFIX = 'vds-rating';
const CSS_STATE = 'vds-state';

const Rating = ({
  ariaLabel,
  className,
  disabled,
  iconChecked,
  iconUnchecked,
  id = shortid.generate(),
  label,
  name,
  numberOfStars,
  onRatingChange,
  rating,
  ...remainingProps
}) => {
  const [rateId, setRateId] = useState(rating);
  const [focusedRateId, setFocusedRateId] = useState(null);
  const [hoveredRateId, setHoveredRateId] = useState(0);
  const rId = useRef(id);

  useEffect(() => {
    setRateId(rating);
  }, [rating]);

  const handleChange = (e, id) => {
    setRateId(id);

    if (typeof onRatingChange === 'function') {
      onRatingChange(e, id);
    }
  };

  const handleMouseOver = (e, id) => {
    setHoveredRateId(id);
  };

  const handleMouseOut = () => {
    setHoveredRateId(0);
  };

  const handleFocus = (e, id = 0) => {
    setFocusedRateId(id);
  };

  return (
    <div
      aria-label={ariaLabel}
      className={classnames(CSS_PREFIX, {
        [`${CSS_STATE}--disabled`]: disabled
      }, className)}
      id={rId.current}
      role="group"
      {...remainingProps}
    >
      {Array.from(Array(numberOfStars)).map((_, k) => (
        <RatingLabel
          ariaLabel={label(k + 1)}
          checked={rateId === k + 1}
          disabled={disabled}
          highlight={k + 1 < rateId}
          hover={k + 1 <= hoveredRateId}
          iconChecked={iconChecked}
          iconUnchecked={iconUnchecked}
          id={`${rId.current}--${k + 1}`}
          isFocused={k + 1 === focusedRateId}
          key={k}
          name={`${name}--${k + 1}`}
          onBlur={handleFocus}
          onChange={e => handleChange(e, k + 1)}
          onFocus={e => handleFocus(e, k + 1)}
          onMouseOver={e => handleMouseOver(e, k + 1)}
          onMouseOut={handleMouseOut}
          value={`${k + 1}`}
        />
      ))}
    </div>
  );
};

Rating.propTypes = {
  /**
   * Aria Label
   */
  ariaLabel: string,

  /**
   * @ignore
   */
  className: string,

  /**
   *  If true, rating buttons will be disabled.
   */
  disabled: bool,

  /**
   * Icon to use when a rating input is checked.
   */
  iconChecked: node,

  /**
   * Icon to use when a rating input is unchecked.
   */
  iconUnchecked: node,

  /**
   * Id for rating component.
   */
  id: oneOfType([number, string]),

  /**
   * Aria label for each rating input.
   */
  label: func,

  /**
   * Name to give each rating input.
   */
  name: string.isRequired,

  /**
   * Number of rating stars to display.
   */
  numberOfStars: number,

  /**
   * Callback that fires when a rating value changes.
   */
  onRatingChange: func,

  /**
   * Rating value when component first renders.
   */
  rating: number
};

Rating.defaultProps = {
  ariaLabel: 'rating',
  iconChecked: <Icon name="favorite-star" resolution="low" role="presentation" />,
  iconUnchecked: <Icon name="favorite-star-border" resolution="low" role="presentation" />,
  label: num => (num === 1 ? '1 star' : `${num} stars`),
  numberOfStars: 5
};

Rating.displayName = 'Rating';

export default Rating;
