import React from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import Icon from '../icon';
import shortid from 'shortid';

const CSS_PREFIX = 'vds-rating';
const CSS_STATE = 'vds-state';

const RatingLabel = ({
  ariaLabel,
  checked,
  className,
  disabled,
  highlight,
  hover,
  iconUnchecked,
  iconChecked,
  id = shortid.generate(),
  isFocused,
  name,
  onChange,
  onFocus,
  onMouseEnter,
  onMouseLeave,
  tabIndex,
  value,
  ...remainingProps
}) => (
  <label
    className={classnames(
      `${CSS_PREFIX}-label`,
      {
        [`${CSS_STATE}--active`]: checked || highlight,
        [`${CSS_STATE}--hover`]: hover,
        [`${CSS_STATE}--focus`]: isFocused
      },
      className
    )}
    htmlFor={id}
    onMouseEnter={onMouseEnter}
    onMouseLeave={onMouseLeave}
    tabIndex={tabIndex}
    {...remainingProps}
  >
    <div
      className={classnames(`${CSS_PREFIX}-star--outline`, {
        [`${CSS_STATE}--hidden`]: highlight || checked || hover
      })}
    >
      {iconUnchecked}
    </div>
    <div
      className={classnames(`${CSS_PREFIX}-star--filled`, {
        [`${CSS_STATE}--hidden`]: !highlight && !checked && !hover
      })}
    >
      {iconChecked}
    </div>
    <input
      aria-label={ariaLabel}
      className={classnames(`${CSS_PREFIX}-radio`)}
      checked={checked}
      disabled={disabled}
      id={id}
      name={name}
      onChange={onChange}
      onFocus={onFocus}
      style={{
        top: 'inherit'
      }}
      type="radio"
      value={value}
    />
  </label>
);

RatingLabel.propTypes = {
  /**
   * Aria label
   */
  ariaLabel: string.isRequired,

  /**
   * Checked
   */
  checked: bool,

  /**
   * @ignore
   */
  className: string,

  /**
   *  Disabled
   */
  disabled: bool,

  /**
   * Highlight
   */
  highlight: bool,

  /**
   * Hover
   */
  hover: bool,

  /**
   * Icon checked
   */
  iconChecked: node,

  /**
   * Icon unchecked
   */
  iconUnchecked: node,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * isFocused
   */
  isFocused: bool,

  /**
   * Name
   */
  name: string.isRequired,

  /**
   * onChange
   */
  onChange: func,

  /**
   * onFocus
   */
  onFocus: func,

  /**
   * onMouseEnter
   */
  onMouseEnter: func,

  /**
   * onMouseLeave
   */
  onMouseLeave: func,

  /**
   * @ignore
   */
  tabIndex: oneOfType([number, string]),

  /**
   * @ignore
   */
  value: string
};

RatingLabel.defaultProps = {
  iconChecked: <Icon name="favorite-star" resolution="low" role="presentation" />,
  iconUnchecked: <Icon name="favorite-star-border" resolution="low"role="presentation" />
};

RatingLabel.displayName = 'RatingLabel';

export default RatingLabel;
